html {
  font-size: 16px;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
body {
  font-family: "sofia-pro", "Roboto", "Helvetica Neue", "Arial", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  color: #4d4541;
  background-color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
a {
  color: #5fc21e;
  text-decoration: none;
}
a:hover {
  color: #51a519;
}
img {
  vertical-align: middle;
}
table {
  border-collapse: collapse;
  background-color: $table-bg;
}
th {
  text-align: left;
}
label {
  display: inline-block;
}
input,
button,
select,
textarea {
  margin: 0;
  line-height: inherit;
  border-radius: 0;
}
textarea {
  resize: vertical;
}
.btn {
  font-size: 14px;
  font-weight: 700;
  padding: 0.5em 1em;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.1s ease;
  border: 1px solid #aaa;
  color: #fff;
  text-transform: uppercase;
}
.btn.primary {
  background-color: #5fc21e;
  border: 1px solid #458c15;
  color: #fff;
}
.btn.primary:hover {
  background-color: #5ab51d;
}
.btn.primary:focus {
  box-shadow: 0px 0px 0px 3px #98e664;
  outline: none;
}
.btn.primary:active {
  box-shadow: inset 0px 1px 5px 0px rgba(0,0,0,0.3);
}
.btn.secondary {
  background-color: #f2f1f0;
  border: 1px solid #cccbca;
  color: #57504b;
}
.btn.secondary:hover {
  background-color: #e6e4e3;
}
.btn.secondary:focus {
  box-shadow: 0px 0px 0px 3px #f0efee;
  outline: none;
}
.btn.secondary:active {
  box-shadow: inset 0px 1px 5px 0px rgba(0,0,0,0.3);
}
.custom-controls-container {
  display: none;
}
a {
  color: #5fc21e;
  font-weight: 500;
}
a:hover,
a:focus,
a:active {
  color: #51a519;
}
ul.tab {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
ul.tab li {
  margin: 0 30px;
}
ul.tab a {
  display: inline-block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  transition: 0.3s;
  font-size: 17px;
  border-bottom: 3px solid transparent;
  position: relative;
  padding-top: 80px;
}
ul.tab a:hover {
  border-bottom: 3px solid $brand-primary;
}
ul.tab a:focus {
  color: #4d4541;
  border-bottom: 3px solid $brand-primary;
}
ul.tab a.active {
  color: #4d4541;
  border-bottom: 3px solid $brand-primary;
}
ul.tab a:before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
}
ul.tab a.farmers:before {
  width: 70px;
  height: 77px;
  background-image: url("/assets/img/agrivi-Farmers.png");
  background-repeat: no-repeat;
  left: calc(50% - 35px);
}
ul.tab a.cooperative:before {
  width: 88px;
  height: 77px;
  background-image: url("/assets/img/agrivi-Cooperative.png");
  background-repeat: no-repeat;
  left: calc(50% - 44px);
}
ul.tab a.enterprise:before {
  width: 87px;
  height: 77px;
  background-image: url("/assets/img/agrivi-Enterprise.png");
  background-repeat: no-repeat;
  left: calc(50% - 44px);
}
ul.tab a.winery:before {
  width: 80px;
  height: 77px;
  background-image: url("/assets/img/agrivi-Winery.png");
  background-repeat: no-repeat;
  left: calc(50% - 40px);
}
.tabcontent {
  background-color: #fff;
  display: none;
  border-top: none;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.15);
  animation: fadeEffect 1s;
  padding: 60px 80px;
  position: relative;
  text-align: left;
  position: relative;
}
.tabcontent h3 {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.tabcontent p {
  max-width: 600px;
}
.tabcontent ul {
  max-width: 580px;
  font-weight: 300;
  margin-bottom: 1rem;
  position: relative;
  z-index: 10;
}
.tabcontent ul li {
  padding-left: 15px;
  margin-bottom: 10px;
  position: relative;
}
.tabcontent ul li:before {
  content: " ";
  width: 4px;
  height: 4px;
  display: block;
  position: absolute;
  left: 0;
  top: 10px;
  background-color: #4d4541;
  border-radius: 50%;
  opacity: 0.8;
}
.tabcontent img {
  position: absolute;
  bottom: 0;
  right: 0;
}
@-moz-keyframes fadeEffect {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@-webkit-keyframes fadeEffect {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@-o-keyframes fadeEffect {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes fadeEffect {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
html {
  opacity: 1;
  transition: opacity 0.5s ease;
}
.wf-loading {
  opacity: 0 !important;
}
.global-container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}
.container {
  margin: 0 auto;
}
.wrap {
  padding: 0 15px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
.block {
  text-align: center;
  padding: 80px 0 0;
}
.block__title {
/* How it works? */
  font-size: 30px;
  color: #4d4541;
  font-weight: 400;
  margin-bottom: 1rem;
}
.block__description {
  max-width: 600px;
  margin: 0 auto;
  font-weight: 300;
  font-size: 16px;
  color: #4d4541;
  margin-bottom: 60px;
}
.illustration-description {
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.15);
  border-radius: 2px;
  background-color: #fff;
  max-width: 770px;
  padding: 40px;
  margin: 0 auto;
  position: relative;
  top: -40px;
}
.illustration-description .leafs {
  position: absolute;
  top: -38px;
  left: calc(50% - 38px);
  background-color: #fff;
  border-radius: 50%;
  height: 76px;
  width: 76px;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.15);
}
.illustration-description .leafs img {
  position: absolute;
  top: 21px;
  left: 24px;
}
.illustration-description p {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 300;
}
.features {
  display: flex;
  flex-flow: row wrap;
  max-width: 970px;
  text-align: left;
  margin: 0 auto;
}
.features .feature {
  width: calc(50% - 30px);
  margin: 0 15px 30px;
}
.features .feature h3 {
  font-size: 24px;
}
.strip {
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.15);
  background-color: #fff;
  padding: 40px;
  margin-top: 60px;
}
.strip h2,
.strip button {
  display: inline-block;
}
.strip h2 {
  margin: 0;
  line-height: 1;
  font-size: 28px;
  font-weight: 300;
  margin-right: 30px;
}
.users {
  background-color: #afa;
}
.flexslider {
  background-color: #afa;
}
.join {
  padding-top: 40px;
}
.join .card {
  padding: 0;
  padding-top: 60px;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.15);
  background-color: #fff;
  position: relative;
}
.join .img {
  position: relative;
  margin-top: 30px;
}
.join .img:before {
  content: " ";
  background: linear-gradient(to bottom, #fff 0%, rgba(255,255,255,0) 100%);
  width: 100%;
  height: 150px;
  position: absolute;
  top: 0;
  left: 0;
}
.help {
  padding-bottom: 80px;
}
.help .block__description {
  margin-bottom: 20px;
}
.footer {
  background-color: #f2f1f0;
  padding: 80px 0;
  font-size: 14px;
  color: #828282;
  font-weight: 400;
}
.footer__links {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.footer .rights {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}
.footer .rights p {
  margin: 0;
  position: relative;
  top: 5px;
}
.footer .rights .logo-gray {
  opacity: 0.3;
  width: 105px;
  height: 40px;
  margin-right: 30px;
}
.footer h4 {
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 600;
}
.footer a {
  color: #828282;
  font-weight: 400;
}
.hero {
  width: 100%;
  height: 540px;
  background-image: url("/assets/img/agrivi-hero-bg@2x.png");
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  background-position: 50% 50%;
}
.hero__text {
  position: absolute;
  bottom: 120px;
  color: #fff;
}
.hero__text h1 {
  font-weight: 300;
  font-size: 34px;
  margin-bottom: 2rem;
}
.nav {
  background-color: transparent;
  height: 60px;
  position: fixed;
  top: 0px;
  width: 100%;
  max-width: 1400px;
  z-index: 1000;
  transform: translateY(10px);
  overflow: hidden;
  transition: transform 0.3s ease 0.1s, background-color 0.2s ease;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav .wrap {
  display: flex;
  align-items: center;
  height: 60px;
  overflow: hidden;
  width: calc(100% + 50px);
}
.nav .logo {
  overflow: hidden;
  background-image: url("/assets/img/agrivi-logo@2x.png");
  width: 105px;
  height: 40px;
  background-size: 100%;
  background-repeat: no-repeat;
  flex-shrink: 0;
}
.nav .language-picker {
  border: 1px solid #fff;
  border-radius: 2px;
  margin-left: 40px;
  opacity: 0.5;
  font-size: 12px;
  appearance: none;
  cursor: pointer;
  background: none;
  padding: 2px 5px;
  color: #fff;
  font-weight: 500;
  transition: all 0.15s ease;
  outline: none;
}
.nav .language-picker:hover {
  opacity: 1;
}
.nav .links {
  margin-left: auto;
  display: flex;
  list-style: none;
  align-items: center;
  animation: move-right 0.3s forwards;
}
.nav .links li {
  margin-right: 30px;
}
.nav .links a {
  color: #fff;
  opacity: 0.9;
  font-weight: 500;
  font-size: 15px;
}
.nav .links a:hover,
.nav .links a:focus {
  opacity: 1;
  border-bottom: 2px solid #5fc21e;
}
.nav .btn {
  animation: move-right 0.6s forwards, fade-out 0.3s forwards;
}
.nav--opaque {
  background-color: #7f5a43;
  transform: translateY(0px);
}
.nav--btn-visible .links {
  animation: move-left 0.4s forwards ease;
}
.nav--btn-visible .btn {
  animation: move-left 0.6s forwards ease, fade-in 0.6s forwards ease;
}
